export default {
    head() {
        return {
            title: `Homes Around ${this.label}`,
        };
    },
    async beforeRouteUpdate(to, from, next) {
        // beforeRouteUpdate runs when user navigates to search page from search page
        const data = await this.$dataApi.getHomesByLocation(
            to.query.lat,
            to.query.lng,
            to.query.start,
            to.query.end
        );
        this.homes = data.json.hits;
        this.label = to.query.label;
        this.lat = to.query.lat;
        this.lng = to.query.lng;
        this.updateMap();
        next();
    },
    async asyncData({ query, $dataApi }) {
        const data = await $dataApi.getHomesByLocation(
            query.lat,
            query.lng,
            query.start,
            query.end
        );
        return {
            label: query.label,
            lat: query.lat,
            lng: query.lng,
            homes: data.json.hits,
        };
    },
    mounted() {
        this.updateMap();
    },
    methods: {
        updateMap() {
            this.$maps.showMap(
                this.$refs.map,
                this.lat,
                this.lng,
                this.getHomeMarkers()
            );
        },
        getHomeMarkers() {
            console.log("this.homes: ", this.homes);
            if (this.homes.length === 0) {
                return null;
            }
            return this.homes.map((home) => {
                return {
                    ...home._geoloc,
                    pricePerNight: home.pricePerNight,
                    id: home.objectID,
                };
            });
        },
        highlightMarker(homeId, isHighlighted) {
            document
                .getElementsByClassName(`home-${homeId}`)[0]
                ?.classList?.toggle("marker-highlight", isHighlighted);
        },
    },
};
